.headerList {
  display: flex;
  list-style: none;
  justify-content: flex-start;
  align-items: center;
  margin: 0;
  height: 50px;
  background: #32393b;
  background: var(--brandColor);
}

.headerList li {
  margin: 0;
}

li a {
  color: white;
}

.logo {
  flex: 0 1;
  font-size: 30px;
  font-weight: 700;
  padding: 10px;
}

.logoItem {
  flex: 1 1;
  margin-right: auto !important;
}

.linkItem {
  padding: 10px;
}

.linkItem a {
  text-decoration: none;
}

@media only screen and (max-width: 900px) {
  .logo {
    font-size: 18px;
  }
}

@media only screen and (max-width: 480px) {
  html {
    font-size: 100%;
  }
}


.menuItem {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.menuWrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 10px;
  text-align: center;
}

@media (max-width: 900px) {
  .menuWrapper {
    grid-template-columns: 1fr;
  }
}

.aboutWrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  border-top: 5px solid black;
  border-bottom: 5px solid black;
}

.aboutUsMission {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #eee;
  border-right: 2.5px solid black;
}

.aboutInfoContainer {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.aboutHeader {
  color: var(--brandColor);
}

.aboutInfoHeader {
  color: #fff;
  padding: 10px;
  text-align: center;
  background: rgba(0, 0, 0, 0.6);
}

.menuButton {
  text-align: center;
  border: none;
  padding: 10px;
  border-radius: 5px;
  background: var(--brandColor);
}

.menuButton a {
  color: #fff;
  text-decoration: none;
}

.aboutSubTitle {
  color: var(--brandColor);
}

.imgWrapper {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-left: 2.5px solid black;
}

img {
  margin: 0;
}

@media (max-width: 900px) {
  .aboutWrapper {
    grid-template-columns: 1fr;
    border: none;
  }

  .aboutUsMission {
    padding: 20px;
    border: none;
  }

  .imgWrapper {
    border: none;
  }
}

.hoursWrapper {
  display: flex;
  flex-direction: column;
}

.hoursHeader {
  margin-top: 20px;
  color: 'lightblue';
  text-align: center;
  color: var(--brandColor);
}

.hoursInfoWrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.hours {
  color: var(--brandColor);
}

span {
  letter-spacing: 3px;
}

.hoursContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media (max-width: 900px) {
  .hoursInfoWrapper {
    grid-template-columns: 1fr;
  }
}

.cafeLandingContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.videoStyle {
  width: 100%;
}

.cafeTitleContainer {
  position: absolute;
  color: white;
  border: 5px solid white;
  padding: 40px;
}

.cafeTitleHeader {
  color: white;
  margin-bottom: 2px;
}

.cafeSubTitle {
  color: white;
  text-align: center;
  margin: 0;
}

.footerWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  background: var(--brandColor);
  padding: 10px;
}

.footerHeader {
  color: #ffffff;
  font-size: 20px;
  margin: 0;
}

